import { useState } from "react";
import { useGame } from "../contexts/GameContext";

const StrengthenListModal = ({
  onEquipmentPartClick,
  isOpen,
  onClose,
  className,
}) => {
  const [activeButton, setActiveButton] = useState(null);
  const { equipment } = useGame();

  //,ActiveButton & onEquipmentPartClick 
  const handleButtonClick = (equipmentPart) => {
    setActiveButton(equipmentPart);
    onEquipmentPartClick(equipmentPart);
  };

  return (
    <>
      <div className={isOpen ? "overlay" : ""}></div>

      <dialog open={isOpen} className={isOpen && `${className}`} tabIndex="0">
        <button onClick={onClose}></button>
        {Object.entries(equipment).map(([part, { item, strengthenLevel }]) => (
          <button
            key={part}
            className={activeButton === part ? "active-button" : ""}
            onClick={() => handleButtonClick(part)}
          >
            <>
              <span>
                {part === "arms"
                  ? `Lv.${strengthenLevel} `
                  : part === "helmet"
                  ? `Lv.${strengthenLevel} `
                  : part === "armor"
                  ? `Lv.${strengthenLevel} `
                  : part === "legArmor"
                  ? `Lv.${strengthenLevel} `
                  : part === "gloves"
                  ? `Lv.${strengthenLevel} `
                  : part === "boots"
                  ? `Lv.${strengthenLevel} `
                  : part === "amulet"
                  ? `Lv.${strengthenLevel} `
                  : part === "ring"
                  ? `Lv.${strengthenLevel} `
                  : ""}
              </span>
              {item ? <span>{item.name}</span> : <span>  </span>}
              {item && item.atk && <span>: {item.atk}</span>}
              {item && item.def && <span>: {item.def}</span>}
              {item && item.resistance && <span>: {item.resistance}</span>}
            </>
          </button>
        ))}
      </dialog>
    </>
  );
};

export default StrengthenListModal;
