import {
  ReplyPotionIcon,
  WeaponIcon,
  ArmorIcon,
  RingIcon,
  ItemIcon,
  EquippedIcon,
} from "./FontAwesomeIcons";
import { useGame } from "../contexts/GameContext";
import MenuModal from "./MenuModal";
import Tooltip from "@mui/material/Tooltip";

const ItemBlock = ({ index, isActive }) => {
  const {
    items,
    isMenuModalOpen,
    handleRightClick,
    handleCloseContextMenu,
    equipment,
  } = useGame();

  //  index itemsitem
  const itemInSlot = items[index];
  // console.log("Index:", index, "Item:", itemInSlot);
  // 
  const isEquipped = Object.values(equipment).some(
    (equip) => equip?.item?.id === itemInSlot?.id
  );

  // 
  const renderIcon = () => {
    if (itemInSlot && itemInSlot.class === "") {
      if (itemInSlot.type === "") {
        return <ReplyPotionIcon />;
      } else {
        return <ItemIcon />;
      }
    }
    if (itemInSlot && itemInSlot.class === "") {
      if (itemInSlot.type === "") {
        return <WeaponIcon />;
      } else if (itemInSlot.type === "" || itemInSlot.type === "") {
        return <RingIcon />;
      } else {
        return <ArmorIcon />;
      }
    }
    // 
    return null;
  };

  //tooltip
  const tooltipTitle = () => {
    if (itemInSlot) {
      return (
        <div>
          <p>: {itemInSlot.name}</p>
          <p>: {itemInSlot.type}</p>
          {itemInSlot.class === "" && (
            <>
              {itemInSlot.rarity && <p>: {itemInSlot.rarity}</p>}
              {itemInSlot.level && <p>: {itemInSlot.level}</p>}
              {itemInSlot.atk && <p>: {itemInSlot.atk}</p>}
              {itemInSlot.def && <p>: {itemInSlot.def}</p>}
              {itemInSlot.resistance && <p>: {itemInSlot.resistance}</p>}
            </>
          )}
          {itemInSlot.class === "" && (
            <>
              {itemInSlot.healAmount && (
                <p>: +{itemInSlot.healAmount}</p>
              )}
            </>
          )}
          <p>: {itemInSlot.quantity}</p>
          <p>: {itemInSlot.sellPrice}</p>
        </div>
      );
    }
  };

  return (
    <>
      <Tooltip
        title={tooltipTitle()}
        arrow
        placement="top"
        componentsProps={{
          tooltip: {
            sx: {
              backgroundColor: "lightblue",
              color: "black",
              fontSize: "0.8rem",
            },
          },
        }}
      >
        <div
          className="grid-item"
          onContextMenu={(e) => handleRightClick(e, index)}
        >
          {itemInSlot && itemInSlot.quantity >= 0 && renderIcon()}
          {itemInSlot && itemInSlot.quantity > 1 && (
            <span className="item-quantity">{"x" + itemInSlot.quantity}</span>
          )}
          {itemInSlot && itemInSlot.class === "" && isEquipped && (
            <span className="item-quantity">
              <EquippedIcon />
            </span>
          )}
          {/*    active  */}
          {itemInSlot && isActive && (
            <MenuModal
              index={index}
              onClose={() => handleCloseContextMenu?.()}
              isOpen={isMenuModalOpen}
            />
          )}
        </div>
      </Tooltip>
    </>
  );
};

export default ItemBlock;
