import { useEffect } from "react";
import { useGame } from "../contexts/GameContext";
import Swal from "sweetalert2";

//GameContext equipment 
//  alert,
//,
//currentStats,currentStats
const StatsEffect = () => {
  const { currentStats } = useGame();
  const {
    baseStats: {
      attack: baseAttack,
      defense: baseDefense,
      resistance: baseResistance,
    },
    equippedStats: {
      attack: equippedAttack,
      defense: equippedDefense,
      resistance: equippedResistance,
    },
  } = currentStats;

  const totalAttack = baseAttack + equippedAttack;
  const totalDefense = baseDefense + equippedDefense;
  const totalResistance = baseResistance + equippedResistance;

  useEffect(() => {
    Swal.fire({
      html: `
         <h2>:</h2>
          <span>${totalAttack}</span>
          <p>  (: ${baseAttack} + : ${equippedAttack})</p>
          <span>${totalDefense}</span>
          <p>  (: ${baseDefense} + : ${equippedDefense})</p>
          <span>${totalResistance}</span>
          <p>  (: ${baseResistance} + : ${equippedResistance})</p>
      `,
      showConfirmButton: false,
      width: 500,
      timer: 500,
    });
  }, [
    currentStats,
    baseAttack,
    baseDefense,
    baseResistance,
    equippedAttack,
    equippedDefense,
    equippedResistance,
    totalAttack,
    totalDefense,
    totalResistance,
  ]);

  return null;
};

export default StatsEffect;
